
#header {
  margin: 0px;
  background-color: #90ee90;
  padding: 0 0 10% 0;
}
#header-name {
  font-size: 5em;
  text-transform: uppercase;
  margin: 0;
  padding: 13% 0 0 0;
}
#header-desc {
  font-size: 1.3em;
  font-weight: bold;
  text-transform: uppercase;
}
#header-nav-list {
  list-style-type: none;
  margin: 0;
  padding: 2.3% 3% 0 0;
  float: right;
}
#header-nav-list li {
  display: inline;
  margin: 8px;
}
#header-nav-list li a {
  padding-top: 2px;
  font-size: 1.2em;
  text-decoration: none;
  color: #2c3e50;
  -webkit-transition: border-top 0.2s;
  transition: border-top 0.2s;
}
#header-nav-list li a:hover {
  border-top: solid 3px;
}



